@import 'common';

.about {
	@extend .container;
	padding-block: 32px 60px;
	background-color: $color-dark;

	@media (min-width: 768px) {
		padding-block: 30px 62px;
		grid-template-rows: auto 567px auto;
	}

	@media (min-width: 1024px) {
		padding-bottom: 60px;
		grid-template-rows: auto 332px auto;
	}

	@media (min-width: 1400px) {
		padding-bottom: 48px;
		grid-template-rows: auto 252px auto;
	}

	&__title {
		margin-block: 0 5px;
		grid-column: 1 / 3;
		font-family: $font-title;
		font-weight: 700;
		font-size: 18px;
		line-height: 23px;
		color: $color-white;

		@media (min-width: 768px) {
			grid-column: 1 / 7;
			grid-row: 1 / 2;
			font-size: 24px;
			line-height: 31px;
		}

		@media (min-width: 1024px) {
			grid-column: 1 / 9;
		}

		@media (min-width: 1400px) {
			grid-column: 1 / 13;
		}
	}

	&__descr {
		margin-block: 0 18px;
		grid-column: 1 / 3;
		font-size: 14px;
		line-height: 25px;
		color: $color-light;

		@media (min-width: 768px) {
			margin-bottom: 0;
			grid-column: 1 / 4;
			grid-row: 2 / 4;
			font-size: 16px;
			line-height: 28px;
		}

		@media (min-width: 1024px) {
			grid-column: 1 / 5;
		}

		@media (min-width: 1400px) {
			grid-column: 1 / 7;
		}
	}

	&__image {
		grid-column: 1 / 3;
		max-height: 400px;
		overflow: hidden;

		@media (min-width: 768px) {
			grid-column: 4 / 7;
			grid-row: 2 / 3;
			max-height: none;
		}

		@media (min-width: 1024px) {
			grid-column: 5 / 9;
		}

		@media (min-width: 1400px) {
			grid-column: 7 / 13;
		}

		* {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__form {
		grid-column: 1 / 3;

		@media (min-width: 768px) {
			grid-column: 4 / 7;
			grid-row: 3 / 4;
		}

		@media (min-width: 1024px) {
			grid-column: 5 / 8;
		}

		@media (min-width: 1400px) {
			grid-column: 7 / 13;
		}
	}

	.form {
		padding-top: 26px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		@media (min-width: 768px) {
			padding-top: 23px;
		}

		@media (min-width: 1024px) {
			padding-top: 20px;
		}

		@media (min-width: 1400px) {
			padding-top: 25px;
			flex-direction: row;
			flex-wrap: wrap;
			gap: 16px 30px;
		}

		&__title {
			margin-block: 0 14px;
			font-family: $font-title;
			font-weight: 400;
			font-size: 18px;
			line-height: 22px;
			color: $color-white;

			@media (min-width: 768px) {
				margin-bottom: 16px;
				font-size: 24px;
				line-height: 22px;
			}

			@media (min-width: 1400px) {
				margin-bottom: 0;
				width: 100%;
			}
		}

		&__input {
			margin-bottom: 20px;
			padding: 12px 23px;
			width: 100%;
			font-size: 14px;
			line-height: 16px;
			color: $color-white;
			background-color: $color-gray;
			border-radius: 0;
			border: none;
			transition: outline .1s ease-in-out;

			@media (min-width: 768px) {
				padding: 16px 23px;
				font-size: 16px;
				line-height: 19px;
			}

			@media (min-width: 1400px) {
				width: auto;
				flex-grow: 1;
			}

			&::placeholder {
				color: $color-light;
				transition: color .1s ease-in-out;
			}

			&:hover::placeholder {
				color: $color-primary;
			}

			&:focus-visible {
				outline: 1px solid $color-primary;
			}

			&:focus-visible::placeholder {
				color: $color-light;
			}

			&:active {
				outline: 1px solid $color-primary-dark;
			}
		}

		&__button {
			// @extend .button-reset;
			padding: 9px 19px;
			font-weight: 700;
			font-size: 14px;
			line-height: 16px;
			color: $color-white;
			background-color: $color-primary;
			border: 3px solid $color-primary;
			transition: border-color .1s ease-in-out,
				background-color .1s ease-in-out;

			@media (min-width: 768px) {
				padding-block: 12.5px;
				font-size: 16px;
				line-height: 19px;
			}

			&:hover {
				background-color: $color-primary-shade;
				border-color: $color-primary-shade;
			}

			&:focus-visible {
				background-color: $color-primary;
				border-color: $color-primary-dark;
				outline: none;
			}

			&:active {
				background-color: $color-primary-dark;
				border-color: $color-primary-dark;
			}
		}
	}

}
